@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    &:focus {
        box-shadow: none;
    }
}

.m-btn-primary{
    padding: 0 16px;
    height: 35px;
    font-weight: 600;
    font-size: 14px;
    line-height: 19px;
    color: #FFFFFF;
    background-color: $primary;
    border-radius: 5px;
    &:hover{
        color: #FFFFFF;
        background-color: darken($primary,10%);
    }
}

.m-btn-gray{
    padding: 0 16px;
    height: 35px;
    font-weight: 600;
    font-size: 14px;
    line-height: 19px;
    color: $gray;
    background-color: #E1E7F0;
    border-radius: 5px;
    &:hover{
        color: darken($gray,10%);
        background-color: darken(#E1E7F0,10%);
    }
}

.m-btn-secondary{
    font-weight: 600;
    font-size: 16px;
    line-height: 22px;
    border-radius: 5px;
    color: #fff;
    background-color: $secondary;
    &:hover{
        color: #fff;
        background-color: darken($secondary,10%);
    }
}

.m-btn-white{
    background: #FFFFFF;
    border-radius: 9px;
    font-size: 16px;
    color: #4E5765;
    &:hover{
        background: #4E5765;
        color: #fff;
    }
}

.m-btn-gray-outline{
    border: 1px solid #CBD2DC;
    border-radius: 10px;
    color: $black;
    &:hover{
        color: $black;
        background-color: #CBD2DC;
    }
}

.swiper-button{
    background: #FFFFFF;
    width: 64px;
    height: 64px;
    border-radius: 50%;
    box-shadow: 0px 25px 33px rgba(225, 231, 240, 0.71);
    &-prev{
        img{
            transform: rotate(-180deg);
        }
    }
}

.text-link{
    font-size: 13px;
    line-height: 20px;
    color: $primary;
}
