.footer{
  margin-top: 50px;
  background-color: #fff;
  padding: 34px 0 28px;
  border-top: 2px solid #DFE5ED;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}


@media screen and (max-width:992px){
  .footer{
    padding: 32px 0 16px;
    &__inner{
      flex-direction: column-reverse;
    }
    .nav{
      display: flex;
      margin-bottom: 34px;
    }
  }
}


@media screen and (max-width:480px){
  .footer{
    .nav{
      justify-content: space-between;
      width: 100%;
      &__item{
        margin-right: 0 !important;
      }
    }
  }
}
