.vacancies{
  .about{
    &__desc{
      max-width: 730px;
      margin-top: 12px;
    }
    &__img{
      margin-top: -19px;
      max-width: 625px;
    }
    &__title2{
      margin-top: -21px;
    }
    &__desc2{
      margin-top: 49px;
    }
  }
  &__grid{
    margin-top: 83px;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - (58px  / 3)));
    gap: 54px 29px;
  }
}

.vacancy{
  padding: 86px 0 50px;
  &__props{
    display: grid;
    grid-template-columns: 1fr 2fr;
    gap: 13px;
    margin-top: 45px;
  }
  &__prop{
    display: flex;
    &-icon{
      flex-shrink: 0;
      width: 24px;
      height: 28px;
      display: flex;
      align-items: center;
      margin-right: 36px;
      img{
        width: 100%;
      }
    }
    &:nth-child(3){
      @media screen and (min-width: 992px){
        grid-row: 1/2;
        grid-column: 2/3;
      }
    }
  }
  &__inner{
    margin-top: 50px;
    padding: 104px 62px;
    background: #FFFFFF;
    box-shadow: 0px 25px 33px rgba(225, 231, 240, 0.71);
    border-radius: 26px;
    position: relative;
  }
  &__smile{
    position: absolute;
    top: 60px;
    right: 0;
  }
  &__subtitle{
    display: flex;
    align-items: center;
    img{
      margin-right: 23px;
      flex-shrink: 0;
    }
  }
  &__group{
    margin-top: 45px;
    &:not(:last-child){
      padding-bottom: 45px;
      border-bottom: 1px solid rgba(225, 231, 240, 0.71);
    }
    &-title{
      font-weight: 600;
      margin-bottom: 45px;
    }
  }
  &__list{
    gap: 30px;
    li{
      max-width: 934px;
      &:before{
        margin-right: 24px;
      }
    }
    &--first{
      li{
        &:first-child{
          padding-right: 140px;
        }
      }
    }
    &--second{
      grid-template-columns: 1fr 1fr;
      gap: 12px;
      li{
        max-width: 455px;
      }
    }
  }
  &__desc{
    line-height: 150%;
    margin-top: -25px;
  }
  &__btn{
    width: 179px;
    height: 43px;
    margin-top: 45px;
  }
}

.vacancy-collection{
  padding: 50px 0;
  overflow: hidden;
  &__title{
    margin-bottom: 35px;
  }
  .swiper{
    &-slide{
      width: 400px;
      max-width: calc(100% - 10px);
    }
  }
}


@media screen and (max-width: 1200px){
  .vacancies{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - 14.5px));
    }
  }
}


@media screen and (max-width: 992px){
  .vacancies{
    .about {
      &__desc {
        margin-top: 23px;
      }
      &__title2{
        margin-top: 51px;
      }
      &__desc2{
        margin-top: 18px;
      }
    }
    &__grid{
      margin-top: 47px;
    }
  }

  .vacancy{
    padding-top: 100px;
    &__title{
      text-align: center;
    }
    &__props{
      grid-template-columns: 100%;
      margin-top: 50px;
      padding: 0 24px;
    }
    &__prop{
      &-icon{
        margin-right: 40px;
      }
    }
    &__inner{
      border-radius: 15px;
      padding: 55px 24px 100px;
    }
    &__group{
      width: calc(100% + 48px);
      margin-left: -24px;
      padding: 0 24px;
      &-title{
        margin-bottom: 30px;
      }
    }
    &__smile{
      display: none;
    }
    &__list{
      padding: 0 !important;
      gap: 17px !important;
      grid-template-columns: 100%;
      li{
        max-width: 100%;
        padding: 0 !important;
      }
    }
    &__desc{
      margin-top: -10px;
    }
  }
}


@media screen and (max-width: 768px){
  .vacancies{
    &__grid{
      grid-template-columns: 100%;
      gap: 29px;
    }
  }
}


@media screen and (max-width: 400px){
  .vacancy{
    &__list{
      li{
        &:before{
          margin-right: 10px;
        }
      }
    }
  }
}
