@import 'vars';
html {
    width: 100%;
}

.wrapper {
    min-height: 100%;
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
}

.content {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
}

.page {
    height: 100%;
    font-family: $font1;
    font-weight: 400;
    color: $black;
    background-color: $bg;
    padding-top: 102px;
    &.fixed-body {
        overflow-y: hidden;
    }
}

.container {
    max-width: 1260px;
    margin: 0 auto;
    padding: 0 30px;
}

a {
    &:hover {
        color: $secondary;
    }
}


@media screen and (max-width: 992px){
    .page{
        padding-top: 108px;
    }

    .container{
        padding: 0 20px;
    }
}

@media screen and (max-width: 400px){
    .container{
        padding: 0 10px;
    }
}
