.catalog-item{
  padding: 74px 0 25px;
  &__title{
    text-align: center;
  }
  &__wrap{
    margin-top: 24px;
    display: flex;
    align-items: center;
  }
  &__rating{
    display: flex;
    align-items: center;
    img{
      flex-shrink: 0;
      margin-right: 10px;
    }
  }
  &__feedback{
    &-num{
      color: $black;
      text-decoration: underline;
    }
  }
  &__badge{
    padding: 0 7px;
    height: 27px;
    margin-left: 12px;
    flex-shrink: 0;
  }
  &__address{
    display: flex;
    align-items: center;
    letter-spacing: -0.05em;
    font-style: normal;
    margin-left: 27px;
    img{
      margin-right: 16px;
      flex-shrink: 0;
    }
  }
  &__favourite{
    display: flex;
    align-items: center;
    margin-left: auto;
    &-icon{
      width: 18px;
      height: 18px;
      flex-shrink: 0;
      background-image: url(../img/favourite2.svg);
      background-repeat: no-repeat;
      background-position: center;
      background-size: 100% auto;
      margin-right: 8px;
    }
    &:hover{
      color: $black;
    }
  }
  &__wrap2{
    display: grid;
    grid-template-columns: auto 40.34%;
    gap: 40px;
    margin-top: 62px;
  }
}

.catalog-gallery{
  margin-top: 23px;
  position: relative;
  &__open{
    z-index: 2;
    position: absolute;
    right: 32px;
    bottom: 32px;
    width: 173px;
    height: 39px;
    border-radius: 8px;
    font-size: 14px;
    img{
      margin-right: 11px;
    }
  }
  &__img{
    display: block;
    width: 100%;
    height: 100%;
  }
  .swiper{
    &-slide{
      img{
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
    @media screen and (min-width: 992px){
      &-wrapper{
        transform: unset !important;
        display: grid !important;
        grid-template-columns: repeat(4,calc(25% - 6px));
        gap: 8px;
        border-radius: 15px;
        overflow: hidden;
      }
      &-slide{
        width: unset !important;
        margin: 0 !important;
        &:nth-child(1){
          grid-row: 1/3;
          grid-column: 1/3;
        }
        &:nth-child(5) ~ .swiper-slide{
          display: none;
        }
      }
      &-pagination{
        display: none;
      }
    }
  }
}

.apart-desc{
  max-width: 630px;
  padding-bottom: 60px;
  border-bottom: 1px solid rgba(225, 231, 240, 0.71);
  &__top{
    display: flex;
    justify-content: space-between;
    padding-bottom: 29px;
    border-bottom: 1px solid rgba(225, 231, 240, 0.71);
  }
  &__info{
    font-size: 18px;
    line-height: 25px;
    color: $gray3;
    margin-top: 8px;
    &-item{
      &:not(:last-child){
        &:after{
          content: '';
          background: linear-gradient(0deg, #141B31, #141B31), linear-gradient(0deg, #141B31, #141B31), #141B31;
          display: inline-block;
          width: 5px;
          height: 5px;
          border-radius: 50%;
          margin-left: 6px;
          transform: translateY(-3px);
        }
      }
    }
  }
  &__text{
    line-height: 150%;
    letter-spacing: 0.02em;
  }
}

.conveniences{
  margin-top: 24px;
  &__items{
    margin-top: 44px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 18px;
  }
  &__item{
    display: flex;
    font-size: 16px;
    line-height: 22px;
    letter-spacing: -0.05em;
  }
  &__icon{
    width: 24px;
    flex-shrink: 0;
    margin-right: 40px;
    margin-top: -1px;
    img{
      max-width: 100%;
    }
  }
  &__all{
    width: 251px;
    height: 44px;
    font-weight: 700;
    background-color: #fff;
    font-size: 16px;
    margin-top: 50px;
  }
}

.apart-form{
  background: rgba(255, 255, 255, 0.59);
  mix-blend-mode: normal;
  box-shadow: 0px 25px 33px rgba(225, 231, 240, 0.71);
  border-radius: 18px;
  padding: 38px 50px;
  position: sticky;
  top: 110px;
  &__price{
    display: flex;
    &-title{
      margin-left: 9px;
    }
  }
  &__grid{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 10px;
    margin-top: 36px;
    .form-group{
      input,
      select{
        height: 44px;
      }
    }
  }
  &__full{
    grid-column: 1/-1;
  }
  &__btn{
    height: 44px;
    width: 100%;
    margin-top: 35px;
    border-radius: 8px;
  }
  &__props{
    margin-top: 42px;
  }
  &__prop{
    display: flex;
    align-items: center;
    justify-content: space-between;
    &:not(:last-child){
      padding-bottom: 22px;
      border-bottom: 1px solid rgba(225, 231, 240, 0.71);
      margin-bottom: 22px;
    }
  }
}

.review{
  overflow: hidden;
  &__items{
    margin-top: 75px;
    .swiper{
      @media screen and (min-width: 992px){
        &-wrapper{
          display: grid;
          grid-template-columns: repeat(2,calc(50% - 15px));
          gap: 44px 30px;
          transform: unset !important;
        }
      }
    }
  }
  &__all{
    width: 267px;
    height: 44px;
    font-weight: 700;
    background-color: #fff;
    font-size: 16px;
    margin-top: 50px;
  }
}

.review-stat{
  margin-top: 40px;
  padding-top: 34px;
  border-top: 1px solid rgba(225, 231, 240, 0.71);
  &__title{
    display: flex;
    align-items: center;
    img{
      margin-right: 10px;
      flex-shrink: 0;
    }
  }
  &__items{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 30px;
    margin-top: 41px;
  }
  &__item{
    max-width: 460px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 16px;
    line-height: 22px;
    letter-spacing: -0.05em;
  }
  &__rating{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 223px;
    flex-shrink: 0;
  }
  &__progress{
    position: relative;
    background: #D9D9D9;
    border-radius: 3px;
    width: 175px;
    height: 4px;
    span{
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      background: #2D77E8;
      border-radius: inherit;
    }
  }
}

.review-form{
  margin-top: 80px;
  background: rgba(255, 255, 255, 0.59);
  mix-blend-mode: normal;
  box-shadow: 0px 25px 33px rgba(225, 231, 240, 0.71);
  border-radius: 18px;
  padding: 46px 50px 52px;
  .review-card__top{
    margin-top: 32px;
  }
  &__items{
    margin-top: 42px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 27px;
  }
  &__item{
    display: flex;
    justify-content: flex-end;
    align-items: center;
    max-width: 430px;
    font-size: 16px;
    line-height: 22px;
    letter-spacing: -0.05em;
    &-title{
      margin-right: auto;
    }
    &-num{
      margin-left: 28px;
    }
  }
  &__field{
    margin-top: 81px;
    textarea{
      resize: none;
      height: 141px;
    }
  }
  &__btn{
    width: 157px;
    height: 44px;
    margin: 28px 0 0 auto;
    font-size: 16px;
  }
}

.owner{
  margin-top: 59px;
  padding-top: 48px;
  border-top: 1px solid rgba(225, 231, 240, 0.71);
  &__top{
    display: flex;
    align-items: center;
  }
  &__img{
    width: 62px;
    height: 62px;
    margin-right: 31px;
    flex-shrink: 0;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 50%;
    }
  }
  &__wrap{
    margin-top: 75px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 23px;
  }
  &__left{
    max-width: 510px;
  }
  &__props{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 18px;
  }
  &__prop{
    display: flex;
    align-items: center;
    line-height: inherit;
    img{
      width: 18px;
      flex-shrink: 0;
      margin-right: 13px;
    }
  }
  &__desc{
    margin-top: 24px;
    line-height: 150%;
  }
  &__subtitle{
    margin: 31px 0;
  }
  &__list{
    letter-spacing: 0.02em;
    li{
      display: flex;
      line-height: inherit;
      &:before{
        content: '';
        width: 6px;
        height: 6px;
        border-radius: 50%;
        background-color: $gray3;
        margin-right: 10px;
        flex-shrink: 0;
        transform: translateY(6px);
      }
      &:not(:last-child){
        margin-bottom: 15px;
      }
    }
  }
  &__btn{
    margin-top: 25px;
    width: 225px;
    height: 44px;
    background-color: #fff;
    font-weight: 700;
    font-size: 16px;
    span{
      text-decoration: underline;
    }
  }
}

.same-apart{
  margin-top: 50px;
  padding-top: 50px;
  border-top: 1px solid rgba(225, 231, 240, 0.71);
}

.same-apart-section{
  padding-bottom: 60px;
}

.catalog-fix-panel{
  display: none;
}


@media screen and (max-width: 1100px){
  .apart-form{
    padding: 22px 30px;
  }
}


@media screen and (max-width: 992px){
  .catalog-item{
    padding-top: 54px;
    &__inner{
      position: relative;
      padding-top: 49px;
    }
    &__badge{
      position: absolute;
      top: 0;
      left: 0;
    }
    &__favourite{
      position: absolute;
      top: 0;
      right: 0;
      height: 27px;
    }
    &__title{
      text-align: left;
    }
    &__wrap{
      margin-top: 21px;
      flex-wrap: wrap;
    }
    &__address{
      width: 100%;
      margin: 5px 0 0;
    }
    &__wrap2{
      grid-template-columns: 100%;
      margin-top: 42px;
      gap: 0;
    }
  }

  .catalog-gallery{
    margin: 50px 0 0 -20px;
    width: calc(100% + 40px);
    position: relative;
    overflow: hidden;
    .swiper{
      &-container{
      }
      &-pagination{
        position: static;
        background: rgba(5, 5, 5, 0.6);
        border-radius: 4px;
        width: unset;
        height: 31px;
        padding: 0 16px;
        display: inline-flex;
        align-items: center;
        color: #FFFFFF;
        font-size: 14px;
        span{
          margin: 0 3px;
        }
      }
    }
    &__img{
      overflow: hidden;
      position: relative;
      padding-bottom: 79.8%;
      img{
        border-radius: inherit;
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        object-fit: cover;
      }
    }
    &__nav{
      position: absolute;
      right: 20px;
      bottom: 50px;
      display: flex;
      align-items: center;
    }
    &__open{
      display: none;
    }
  }

  .apart-desc{
    max-width: 100%;
    padding-bottom: 35px;
  }

  .conveniences{
    margin-top: 43px;
  }

  .apart-form{
    display: none;
  }

  .review-stat{
    margin-top: 34px;
    padding-top: 43px;
    &__items{
      display: none;
    }
  }

  .review{
    &__items{
      margin-top: 29px;
      .swiper{
        &-wrapper{
          align-items: stretch;
        }
        &-slide{
          width: 322px;
          height: auto;
        }
      }
    }
  }

  .review-form{
    padding: 20px 30px;
    &__items{
      grid-template-columns: 100%;
    }
    &__item{
      max-width: 100%;
    }
    &__field{
      margin-top: 40px;
    }
  }

  .owner{
    margin-top: 50px;
    padding-top: 50px;
    &__wrap{
      grid-template-columns: 100%;
      margin-top: 20px;
    }
    &__left{
      max-width: 100%;
    }
    &__props{
      grid-template-columns: 100%;
    }
  }

  .same-apart{
    border: none;
  }

  .catalog-fix-panel{
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    background: #FFFFFF;
    box-shadow: 0px 20px 20px rgba(130, 145, 167, 0.27);
    padding: 32px 54px 32px 32px;
    display: flex;
    align-items: center;
    z-index: 20;
    .apart-form__price{
      white-space: nowrap;
    }
    &__btn{
      margin-left: 52px;
      height: 44px;
      flex-grow: 1;
    }
  }
}


@media screen and (max-width: 576px){
  .conveniences,{
    &__all{
      width: 100%;
    }
  }

  .review{
    &__all{
      width: 100%;
      margin-top: 46px;
    }
  }

  .review-form{
    &__btn{
      width: 100%;
      margin-top: 20px;
    }
  }

  .owner{
    &__top{
      flex-direction: row-reverse;
      justify-content: space-between;
    }
    &__img{
      margin: 0 0 0 20px;
    }
    &__btn{
      width: 100%;
    }
  }
}


@media screen and (max-width: 480px){
  .conveniences{
    &__items{
      grid-template-columns: 100%;
    }
  }

  .catalog-fix-panel{
    padding: 20px;
    &__btn{
      margin-left: 30px;
    }
  }
}


@media screen and (max-width: 400px){
  .catalog-gallery{
    width: calc(100% + 20px);
    margin-left: -10px;
    &__nav{
      right: 10px;
      bottom: 25px;
    }
  }
}
