@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";

.m-dropdown{
  position: relative;
  display: grid;
  grid-template-columns: 100%;
  background: #FFFFFF;
  border: 1px solid #F6F7F8;
  box-shadow: 0px 25px 31px rgba(225, 231, 240, 0.71);
  border-radius: 8px;
  &:after{
    content: '';
    position: absolute;
    z-index: 1;
    top: -9px;
    left: 25px;
    width: 20px;
    height: 20px;
    border-radius: 4px;
    background-color: #fff;
    box-shadow: 0px 25px 31px rgba(225, 231, 240, 0.71);
    border: 1px solid #F6F7F8;
    transform: rotate(-45deg);
  }
  &:before{
    content: '';
    position: absolute;
    background-color: inherit;
    width: 40px;
    height: 30px;
    top: 0;
    left: 20px;
    z-index: 2;
  }
}

.tabs{
  display: flex;
  justify-content: center;
  overflow-x: auto;
  padding-bottom: 15px;
  &-wrap{
    display: flex;
    align-items: center;
  }
}

.tab{
  border: 1px solid #CBD2DC;
  display: flex;
  align-items: center;
  padding: 0 15px;
  height: 44px;
  font-size: 16px;
  color: $gray3;
  transition: all .3s;
  white-space: nowrap;
  flex-shrink: 0;
  &:hover{
    background: #E1E7F0;
    cursor: pointer;
  }
  &:not(:last-child){
    border-right: none;
  }
  &:first-child{
    border-radius: 5px 0px 0px 5px;
  }
  &:last-child{
    border-radius: 0px 5px 5px 0px;
  }
  &._active{
    background: #89C1F0;
    border-color: #89C1F0;
    color: #fff;
    pointer-events: none;
  }
  &-content{
    display: none;
    &._active{
      display: block;
    }
  }
}


@media screen and (max-width: 992px){
  .tabs{
    width: calc(100% + 40px);
    padding-left: 20px;
    padding-right: 20px;
    margin-left: -20px;
    justify-content: unset;
    &-wrap{
      margin-left: auto;
      margin-right: auto;
    }
  }
}


@media screen and (max-width: 400px){
  .tabs{
    width: calc(100% + 20px);
    padding-left: 10px;
    padding-right: 10px;
    margin-left: -10px;
  }
}
