.edu--cooperate{
  overflow: hidden;
  .edu{
    @media screen and (min-width: 769px){
      &__inner{
        grid-template-columns: 45.5% 54.5%;
      }
      &__desc{
        margin-top: 39px;
        max-width: 100%;
        width: 100%;
      }
      &__img{
        img{
          width: 128.75%;
          margin: 0 0 0 -6.5%;
        }
      }
    }
  }
}

.effective{
  padding: 50px 0;
  &__title{
    text-align: center;
    max-width: 795px;
    margin: 0 auto;
    line-height: 110%;
  }
  &__desc{
    margin: 12px auto 0;
    max-width: 772px;
    text-align: center;
  }
  &__grid{
    margin-top: 55px;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 16.5px));
    gap: 39px 33px;
  }
}

.offer{
  padding: 50px 0;
  &__title{
    text-align: center;
  }
  &__desc{
    margin: 12px auto 0;
    max-width: 772px;
    text-align: center;
  }
  &__grid{
    margin-top: 50px;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - (52px / 3)));
    gap: 26px;
  }

}


@media screen and (max-width: 1100px){
  .offer{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - 13px));
    }
    .offer-card{
      height: 100%;
    }
  }
}


@media screen and (max-width: 992px){
  .edu--cooperate{
    .edu{
      &__img{
        margin: 0 auto 0;
        img{
          width: 120%;
        }
      }
      &__desc{
        width: 90%;
        margin: 47px 0;
      }
    }
  }

  .effective{
    &__grid{
      grid-template-columns: 100%;
      gap: 28px;
    }
  }
}


@media screen and (max-width: 768px){
  .offer{
    &__grid{
      grid-template-columns: 100%;
    }
  }
}
