.main{
  padding: 120px 0 286px;
  position: relative;
  &__images{
    position: absolute;
    right: 0;
    bottom: 0;
    width: 100%;
    height: calc(100% + 102px);
    pointer-events: none;
    padding-top: 93px;
    overflow: hidden;
  }
  &__smile{
    max-width: 100%;
    position: absolute;
    top: -144px;
    left: calc((100% - 1200px) / 2 + 110px);
  }
  &__couple{
    max-width: 632px;
    position: relative;
    z-index: 2;
    margin-left: auto;
    display: block;
    transform: translateX(140px);
  }
  &__content{
    max-width: 790px;
    position: relative;
    z-index: 3;
  }
  &__desc{
    font-size: 20px;
    line-height: 135%;
    margin-top: 2px;
  }
  &__form{
    padding: 25px;
    margin-top: 29px;
    background: #FFFFFF;
    mix-blend-mode: multiply;
    box-shadow: 0px 25px 33px rgba(225, 231, 240, 0.71);
    border-radius: 18px;
    display: grid;
    grid-template-columns: 1fr 1fr .9fr .6fr;
    gap: 27px;
    .form-group{
      select{
        background-image: url(../img/select-arrow.svg);
      }
    }
  }
  &__btn{
    height: 40px;
    &:hover{
      transform: translate(0px, -4px);
      box-shadow: 0 4px 20px 0 rgba($secondary, 0.43);
    }
  }
}

.advantages{
  margin-top: -127px;
  padding: 50px 0;
  overflow: hidden;
  .swiper{
    &-container{
      position: relative;
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
      width: 279px;
    }
    &-button{
      &-disabled{
        opacity: 0;
      }
    }
  }
}

.feedback{
  padding: 50px 0;
  &__inner{
    padding-right: 5%;
    overflow: hidden;
    border-radius: 27px;
    background: #D7EEFF;
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
  }
  &__content{
    padding: 62px 45px;
    max-width: 530px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__title{
    max-width: 320px;
    margin: 0 auto 24px 0;
  }
  &__desc{
    margin-right: auto;
  }
  &__link{
    padding: 0 18px;
    height: 43px;
    margin: 52px auto 0 0;
  }
  &__img{
    width: 44.8%;
    img{
      width: 100%;
    }
  }
}

.educate{
  .feedback{
    &__inner{
      padding-right: 11.5%;
      background: #D0FFD1;
      align-items: center;
    }
    &__title{
      max-width: 345px;
    }
    &__img{
      width: 300px;
      align-self: unset;
    }
  }
}

.apart{
  padding: 50px 0;
  &__title{
    text-align: center;
  }
  &__tabs{
    margin-top: 28px;
  }
  &__grid{
    margin-top: 40px;
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 15px));
    gap: 32px 20px;
  }
}

.companies{
  padding: 50px 0;
  overflow: hidden;
  .swiper{
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      width: unset;
      display: flex;
      align-items: center;
      height: auto;
      img{
        display: block;
        max-width: 100%;
        max-height: 66px;
      }
    }
  }
}

.cooperate{
  padding: 50px 0;
  &__title{
    text-align: center;
  }
  &__desc{
    text-align: center;
    margin-top: 27px;
  }
  &__grid{
    margin-top: 51px;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - (64px  / 3)));
    gap: 29px 32px;
  }
}


@media screen and (max-width: 1200px){
  .apart{
    &__grid{
      grid-template-columns: repeat(3,calc(100% / 3 - 40px / 3));
    }
  }
}


@media screen and (max-width: 1100px){
  .cooperate{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - 16px));
    }
  }
}


@media screen and (max-width: 992px){
  .main{
    padding: 46px 0 50px;
    &__img{
      height: calc(100% + 108px);
    }
    &__couple{
      display: none;
    }
    &__smile{
      top: 26px;
      left: unset;
      right: -20%;
    }
    &__title{
      span{
        display: block;
        line-height: inherit;
      }
    }
    &__desc{
      font-size: 18px;
      line-height: 25px;
      margin-top: 25px;
    }
    &__form{
      grid-template-columns: 1fr 1fr;
      gap: 12px 26px;
      padding: 22px 20px 36px;
      margin-top: 26px;
    }
    &__btn{
      grid-column: 1/-1;
      margin-top: 6px;
    }
    &__full{
      grid-column: 1/-1;
    }
  }

  .advantages{
    margin: 0;
  }

  .feedback{
    &__inner{
      display: block;
      padding: 0;
    }
    &__content{
      padding: 55px 40px 0;
      max-width: unset;
      position: relative;
      z-index: 2;
    }
    &__title{
      margin-bottom: 13px;
    }
    &__link{
      margin-top: 42px;
    }
    &__img{
      width: 100%;
      margin-top: -10%;
    }
  }

  .educate{
    .feedback{
      &__inner{
        padding-right: 0;
        padding-bottom: 40px;
      }
      &__img{
        margin: 13px auto 0;
        width: 255px;
      }
    }
  }

  .apart{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - 10px));
    }
  }
}


@media screen and (max-width: 768px){
  .cooperate{
    &__grid{
      grid-template-columns: 100%;
      margin-top: 45px;
    }
  }
}


@media screen and (max-width: 576px){
  .advantages{
    .container{
      position: relative;
    }
    .swiper{
      &-container{
        position: static;
        width: calc(100% - 150px);
      }
      &-slide{
        width: 100%;
        &-active{
          &~.swiper-slide{
            filter: blur(2px);
          }
        }
      }
    }
  }

  .apart{
    &__grid{
      grid-template-columns: 100%;
    }
  }
}


@media screen and (max-width: 480px){
  .advantages{
    .swiper-container{
      width: calc(100% - 50px);
    }
  }


}


@media screen and (max-width: 400px){
  .feedback{
    &__content{
      padding: 30px 20px 0;
    }
  }
}
