input[type="date"] {
  position: relative;
  background-image: url(../img/calender.svg);
  //background-image: url("");
  background-position: right 10px center;
  background-repeat: no-repeat;
  background-size: 24px auto;
  padding-right: 28px;
  font-size: 14px;
  font-weight: 600;
}

input[type="date"]::-webkit-calendar-picker-indicator {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: auto;
  height: auto;
  color: transparent;
  background: transparent;
  opacity: 0;
}

input[type="date"]::-webkit-inner-spin-button,
input[type="date"]::-webkit-clear-button {
  z-index: 1;
}

input[type="date"]::before {
  content: attr(data-placeholder);
  width: 100%;
  font-size: inherit;
}

input[type="date"]:focus::before,
input[type="date"]:valid::before {
  display: none
}

.form-group {
  position: relative;
  margin: 0;
  input,
  textarea,
  select{
    background-color: #fff;
    width: 100%;
    height: 40px;
    border-radius: 5px;
    padding: 19px 24px 0 24px;
    font-size: 14px;
    line-height: 171%;
    font-weight: 600;
    border: 1px solid #CFD8E4;
    color: $gray3;
    font-family: inherit;
    &:hover,
    &:focus,
    &:active {
      border-color: $primary;
    }
    &:focus,
    &.input-active {
      &+label {
        left: 24px;
        font-size: 11px;
        line-height: 15px;
        top: 13px;
        color: $gray2;
      }
    }
  }
  label {
    transition: all .1s;
    pointer-events: none;
    position: absolute;
    left: 25px;
    top: 50%;
    transform: translateY(-50%);
    font-size: 14px;
    line-height: 171%;
    font-weight: 600;
    color: $gray3;
  }
  textarea {
    min-height: 100px;
    resize: vertical;
    padding-top: 12px;
    padding-bottom: 12px;
    &+label {
      top: 12px;
      opacity: 0;
    }
    &:focus,
    &.input-active {
      &+label {
        opacity: 1;
      }
    }
  }
  select{
    -webkit-appearance: none;
    -moz-appearance: none;
    -ms-appearance: none;
    -o-appearance: none;
    appearance: none;
    background-image: url(../img/select-arrow2.svg);
    background-position: right 24px center;
    background-repeat: no-repeat;
    padding-right: 50px;
    padding-top: 17px;

    &::-ms-expand {
      display: none; // IE
    }
  }
  .input--error{
    border-color: $secondary;
    &:focus,
    &:hover{
      border-color: $secondary;
    }
  }
}

.form-group2{
  label{
    position: static;
    transform: unset !important;
    font-size: 12px !important;
    line-height: 15px !important;
    color: #000000 !important;
    margin-bottom: 11px;
    display: block;
  }
  input{
    height: 54px;
    padding: 0 18px;
    font-size: 12px;
    border-radius: 8px;
    color: $gray3;
    &::placeholder{
      color: $gray3;
    }
  }
}


.m-check {
  position: relative;
  input {
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
    &:checked+label {
      &::before {
        border-color: $primary;
        background-color: $primary;
      }
    }
  }
  label {
    padding-left: 34px;
    position: relative;
    min-height: 18px;
    font-size: 16px;
    line-height: 23px;
    cursor: pointer;
    &:before {
      content: '';
      position: absolute;
      z-index: 1;
      border-radius: 3px;
      border: 1px solid #CFD8E4;
      width: 18px;
      height: 18px;
      left: 0;
      top: 50%;
      background: #FFFFFF;
      transform: translateY(-50%);
      transition: all .100s;
    }
  }
}

.sort-select{
  select{
    -webkit-appearance: none;
    -moz-appearance: none;
    -ms-appearance: none;
    -o-appearance: none;
    appearance: none;
    background-image: url(../img/select-arrow2.svg);
    background-position: right 24px center;
    background-repeat: no-repeat;
    padding: 0 50px 0 25px;
    border: none;
    background-color: transparent; font-size: 16px;
    line-height: 23px;
    color: $primary;
    margin-left: -25px;
  }
}

.rating {
  display: flex;
  align-items: center;
  flex-direction: row-reverse;
  &--read-only{
    pointer-events: none;
  }
  &__item {
    transition: all .3s;
    font-size: 25px;
    color: #CFCFCF;
    &:not(:first-child) {
      margin-right: 6px;
    }
    &:hover {
      cursor: pointer;
      path{
        fill: #FFB800;
      }
      &~.rating__item {
        path{
          fill: #FFB800;
        }
      }
    }
  }
}

.rating[data-total-rating="1"] .rating__item:nth-child(n+5),
.rating[data-total-rating="2"] .rating__item:nth-child(n+4),
.rating[data-total-rating="3"] .rating__item:nth-child(n+3),
.rating[data-total-rating="4"] .rating__item:nth-child(n+2),
.rating[data-total-rating="5"] .rating__item:nth-child(n+1) {
  path{
    fill: #FFB800;
  }
}
