.display1{
  font-size: 49px;
  line-height: 137%;
  font-weight: 700;
  letter-spacing: -0.05em;
}

.display2{
  font-weight: 700;
  font-size: 40px;
  letter-spacing: -0.05em;
}

.display3{
  font-weight: 700;
  font-size: 30px;
  line-height: 41px;
  letter-spacing: -0.05em;
}

.display4{
  font-weight: 600;
  font-size: 24px;
  line-height: 125%;
}

.display5{
  font-weight: 700;
  font-size: 20px;
  line-height: 27px;
  letter-spacing: -0.05em;
}

.accent-text{
  color: $primary;
}

.secondary-text{
  color: $secondary;
}

.gray-text{
  color: $gray;
}

.gray-text3{
  color: $gray3;
}

.body-text,
.body-text2{
  p{
    line-height: inherit;
    &:not(:last-child){
      margin-bottom: 25px;
    }
  }
}

.body-text{
  font-size: 16px;
  line-height: 138%;
}

.body-text2{
  font-size: 14px;
  line-height: 19px;
}

ol{
  counter-reset: list;
  display: grid;
  grid-template-columns: 100%;
  gap: 20px;
  li{
    line-height: inherit;
    display: flex;
    &:before{
      content: counter(list) ". ";
      counter-increment: list;
      margin-right: 5px;
    }
  }
}

.m-list{
  display: grid;
  grid-template-columns: 100%;
  gap: 18px;
  li{
    display: flex;
    font-size: 16px;
    line-height: 150%;
    color: $gray3;
    &:before{
      content: '';
      width: 24px;
      height: 24px;
      flex-shrink: 0;
      background-image: url(../img/list-icon.svg);
      background-size: 100% auto;
      background-repeat: no-repeat;
      background-position: center;
      margin-right: 20px;
    }
  }
}


@media screen and (max-width: 992px){
  .display1{
    font-size: 40px;
    line-height: 106%;
  }

  .display2{
    font-size: 36px;
    line-height: 108%;
  }
}


@media screen and (max-width: 480px){
  .display1{
    font-size: 36px;
  }

  .display2{
    font-size: 34px;
  }
}
