.catalog{
  padding: 90px 0 50px;
  &__title{
    text-align: center;
  }
  &__desc{
    text-align: center;
  }
  .main{
    &__form{
      margin-top: 41px;
      @media screen and (min-width: 992px){
        grid-template-columns: auto auto 232px 122px;
      }
    }
    &__btn{
      height: 44px;
    }
  }
  &__type{
    margin-top: 57px;
    display: none;
  }
  &__radios{
    display: flex;
    overflow-x: auto;
    padding-bottom: 15px;
    &-wrap{
      display: flex;
      align-items: center;
      margin: 0 auto;
    }
  }
  &__radio{
    position: relative;
    flex-shrink: 0;
    input{
      position: absolute;
      top: 0;
      left: 0;
      z-index: 2;
      width: 100%;
      height: 100%;
      opacity: 0;
      &:checked{
        pointer-events: none;
        &+label{
          background: #89C1F0;
          border-color: #89C1F0;
          color: #fff;
          pointer-events: none;
        }
        &:hover{
          &+label{
            background: #89C1F0;
            border-color: #89C1F0;
          }
        }
      }
      &:hover{
        cursor: pointer;
        &+label{
          background: #E1E7F0;
        }
      }
    }
    label{
      border: 1px solid #CBD2DC;
      display: flex;
      align-items: center;
      padding: 0 15px;
      height: 44px;
      font-size: 16px;
      color: $gray3;
      transition: all .3s;
      white-space: nowrap;
    }
    &:first-child{
      label{
        border-radius: 5px 0px 0px 5px;
      }
    }
    &:last-child{
      label{
        border-radius: 0px 5px 5px 0px;
      }
    }
  }
  &__filter{
    margin-top: 25px;
    width: calc(100% - 176px);
    position: relative;
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap: 19px;
    &-col{
      display: grid;
      grid-template-columns: 100%;
      gap: 14px;
      &:last-child{
        position: absolute;
        left: 100%;
        margin-left: 19px;
        top: 0;
        width: 157px;
      }
    }
    &-btn{
      height: 44px;
      font-size: 16px;
    }
  }
  .form-group{
    select,
    input{
      height: 44px;
    }
  }
  &__content{
    margin-top: 40px;
  }
  .apart__grid{
    margin: 0;
  }
  &__items{
    position: relative;
    margin: 20px 0;
    &:before{
      content: '';
      position: absolute;
      width: calc(100% + 40px);
      height: calc(100% + 40px);
      left: 50%;
      top: 50%;
      transform: translate(-50%,-50%);
      border: 1px dashed #9747FF;
      border-radius: 5px;
    }
  }
  &__nav{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 58px;
  }
  &__more{
    padding: 0 21px;
    height: 46px;
    font-size: 16px;
  }
  &__detail,
  &__content{
    display: none;
  }
}

.search-detail{
  margin-top: 24px;
  &__show{
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
  &__toggle{
    display: flex;
    align-items: center;
    font-size: 16px;
    line-height: 23px;
    color: $primary;
    svg{
      margin-left: 10px;
    }
    &:hover{
      opacity: .9;
    }
  }
  &__hidden{
    margin-top: 15px;
    display: none;
  }
  &__checks{
    width: calc(100% - 176px);
    column-count: 3;
    column-gap: 19px;
  }
  &__check{
    display: inline-block;
    width: 100%;
    &:not(:last-child){
      margin-bottom: 25px;
    }
  }
  &._open{
    .search-detail__toggle{
      svg{
        transform: rotate(180deg);
      }
    }
  }
}


@media screen and (max-width: 992px){
  .catalog{
    padding-top: 40px;
    &__title{
      text-align: left;
      max-width: 350px;
    }
    &__desc{
      max-width: 265px;
      margin-top: 25px;
      text-align: left;
    }
    .main__form{
      margin-top: 30px;
    }
    &__type{
      margin-top: 54px;
    }
    &__filter{
      width: 100%;
      margin-top: 15px;
      grid-template-columns: 100%;
      &-col{
        gap: 19px;
        &:nth-child(4){
          margin: 15px 0 0;
          position: static;
          width: 100%;
          gap: 14px;
        }
      }
    }
    &__content{
      margin-top: 103px;
    }
    &__items{
      margin: 28px 0 0;
      &:before{
        display: none;
      }
    }
  }

  .search-detail{
    margin-top: 36px;
    &__show{
      justify-content: unset;
    }
    &__checks{
      width: 100%;
      column-count: 2;
    }
  }
}
