.course{
  padding: 100px 0 50px;
  .event-card2__tags{
    margin: 0 0 10px;
  }
  &__tabs{
    margin-top: 63px;
    justify-content: unset;
  }
  &__content{
    margin-top: 17px;
    background: #FFFFFF;
    border-radius: 8px;
  }
}


.course-info{
  margin-top: 51px;
  display: grid;
  grid-template-columns: 1fr 290px .8fr 275px .9fr;
  gap: 17px;
}

.course-card{
  padding: 22px 25px;
  background: #FFFFFF;
  border-radius: 20px;
  &__title{
    font-size: 16px;
    line-height: 20px;
    letter-spacing: -0.05em;
    color: $gray3;
  }
  &__time{
    display: flex;
    align-items: center;
    margin-top: 24px;
    img{
      flex-shrink: 0;
      margin-right: 10px;
      width: 24px;
    }
    &-title{
      font-weight: 600;
      font-size: 17px;
      line-height: 20px;
      color: $primary;
    }
  }
  &__wrap{
    display: flex;
    justify-content: space-between;
    margin-top: 24px;
  }
  &__desc{
    font-size: 13px;
    line-height: 20px;
    color: $gray;
    margin-top: 2px;
  }
  &__stat{
    display: flex;
    align-items: center;
    &-num{
      color: $primary;
      font-weight: 700;
      font-size: 21px;
      line-height: 20px;
    }
  }
  &__chart{
    width: 40px;
    flex-shrink: 0;
    margin-right: 8px;
    svg{
      width: 100%;
      height: auto;
      transform: rotate(-90deg);
    }
  }
  &__ball{
    text-align: right;
  }
  &__subtitle{
    font-weight: 700;
    font-size: 36px;
    line-height: 17px;
  }
  &__percent{
    margin-top: 25px;
  }
  &__props{
    margin-top: 15px;
  }
  &__prop{
    display: flex;
    align-items: center;
    font-size: 16px;
    line-height: 17px;
    letter-spacing: -0.05em;
    &:not(:last-child){
      margin-bottom: 10px;
    }
    img{
      margin-right: 18px;
      flex-shrink: 0;
      width: 24px;
      display: block;
    }
  }
}

.course-desc{
  padding: 31px 32px 43px;
  position: relative;
  &__smile{
    position: absolute;
    top: 31px;
    right: 0;
  }
  &__props{
    display: grid;
    grid-template-columns: 100%;
    gap: 2.5px;
    max-width: 420px;
  }
  &__prop{
    display: grid;
    grid-template-columns: auto 140px;
    align-items: center;
    gap: 5px;
    padding: 5px 23px;
    min-height: 42px;
    font-size: 16px;
    line-height: 120%;
    &:nth-child(odd){
      background: #F5F5F5;
    }
  }
  &__info{
    margin-top: 40px;
    padding-top: 40px;
    border-top: 1px solid #E0E1E1;
  }
  &__text{
    margin-top: 17px;
  }
  &__img{
    display: block;
    margin-top: 57px;
    max-width: 731px;
    width: 100%;
  }
}

.chapter{
  padding: 58px 31px 55px;
  table{
    width: 100%;
    border-spacing: 0 15px;
  }
  tr{
    &:nth-child(odd){
      td{
        background: rgba(225, 231, 240, 0.29);
      }
    }
  }
  td{
    padding-top: 8px;
    padding-bottom: 8px;
    &:first-child{
      padding-left: 14px;
      padding-right: 20px;
    }
    &:nth-child(2){
      width: 137px;
    }
    &:nth-child(3){
      width: 74px;
    }
    &:last-child{
      width: 200px;
      padding-right: 23px;
    }
  }
  &__title{
    max-width: 500px;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.05em;
    color: $gray3;
    display: flex;
    &-num{
      margin-right: 5px;
    }
  }
  &__btn{
    width: 95px;
    height: 28px;
    font-weight: 700;
    font-size: 13px;
    margin-left: auto;
  }
  &__num{
    font-size: 16px;
    line-height: 150%;
    letter-spacing: -0.05em;
    font-weight: 700;
  }
  &__ball{
    font-size: 10px;
    line-height: 120%;
    color: $gray;
  }
}

.resources{
  padding: 58px 35px 66px;
  &__grid{
    display: grid;
    grid-template-columns: repeat(3,calc(33.33% - (50px / 3)));
    gap: 50px 25px;
    max-width: 899px;
  }
}

.resource-card{
  &__img{
    position: relative;
    padding-bottom: 60.1%;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      position: absolute;
      top: 0;
      left: 0;
    }
  }
  &__title{
    margin-top: 16px;
    letter-spacing: -0.05em;
    font-size: 16px;
    line-height: 150%;
    display: flex;
    align-items: center;
    img{
      margin-right: 16px;
    }
  }
}

.chat{
  padding: 37px 34px 59px;
  &__top{
    display: flex;
    align-items: center;
    margin-top: 33px;
  }
  &__avatar{
    width: 40px;
    height: 40px;
    flex-shrink: 0;
    margin-right: 25px;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 50%;
    }
  }
  &__name,
  &__btn{
    font-weight: 500;
    font-size: 16px;
    line-height: 20px;
    letter-spacing: 0.02em;
  }
  &__btn{
    margin-left: 27px;
    display: flex;
    align-items: center;
    img{
      margin-right: 9px;
      flex-shrink: 0;
    }
    span{
      text-decoration-line: underline;
    }
  }
  &__table{
    margin-top: 76px;
    table{
      width: 100%;
      border-collapse: collapse;
    }
    th{
      color: #000000;
      text-align: left;
      padding-top: 18px;
      padding-bottom: 18px;
      border-bottom: 6px solid #F2F7FC;
    }
    th,
    td{
      font-weight: 500;
      font-size: 16px;
      line-height: 20px;
      letter-spacing: 0.02em;
      &:not(:last-child){
        padding-right: 10px;
      }
    }
    td{
      padding-top: 20px;
      padding-bottom: 20px;
      vertical-align: top;
      border-bottom: 1px solid #E0E1E1;
    }
    &-title{
      font-weight: 400;
      color: $gray3;
      max-width: 370px;
      display: block;
      letter-spacing: -0.05em;
    }
  }
}


@media screen and (max-width:1200px){
  .course-info{
    grid-template-columns: repeat(6,1fr);
    @media screen and (min-width:769px){
      .course-card{
        grid-column:  span 2;
        &:nth-child(2){
          grid-column: 1/4;
          grid-row: 1/2;
        }
        &:nth-child(4){
          grid-column: 4/7;
          grid-row: 1/2;
        }
      }
    }
  }
}


@media screen and (max-width:992px){
  .course{
    padding-bottom: 0;
    &__content{
      width: calc(100% + 40px);
      margin: 32px 0 0 -20px;
    }
  }

  .course-info{
    margin-top: 58px;
  }

  .course-desc{
    padding: 58px 35px 98px;
    &__info{
      padding-top: 20px;
    }
    &__img{
      margin-top: 24px;
    }
    &__smile{
      display: none;
    }
  }

  .chapter{
    padding: 49px 33px 68px;
    table,
    tbody{
      display: block;
    }
    tr{
      padding: 20px;
      display: grid;
      grid-template-columns: 1.5fr 1fr 1fr 1fr;
      gap: 20px 10px;
      width: 100%;
      &:nth-child(odd){
        background: rgba(225, 231, 240, 0.29);
      }
    }
    td{
      display: block;
      width: 100% !important;
      padding: 0 !important;
      background-color: transparent !important;
      &:nth-child(1){
        grid-column: 1/-1;
      }
      &:nth-child(2){
        padding-left: 18px !important;
      }
    }
  }

  .resources{
    padding-bottom: 98px;
    &__grid{
      grid-template-columns: repeat(2,calc(50% - 12.5px));
    }
  }

  .chat{
    padding: 39px 22px 76px;
  }
}


@media screen and (max-width:768px){
  .course-info{
    grid-template-columns: repeat(2,1fr);
    @media screen and (min-width:480px){
      .course-card{
        &:nth-child(2){
          grid-column: 1/-1;
          grid-row: 2/3;
        }
      }
    }
    &__title{
      font-size: 36px;
      line-height: 108%;
    }
  }

  .course-card{
    padding: 20px;
    &__prop{
      font-size: 14px;
      line-height: 17px;
      img{
        margin-right: 6px;
      }
    }
  }

  .resources{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .chat{
    &__top{
      display: block;
      margin-top: 28px;
      position: relative;
    }
    &__avatar{
      width: 49px;
      height: 49px;
      position: absolute;
      top: 50%;
      right: 0;
      transform: translateY(-50%);
    }
    &__btn{
      margin: 6px 0 0;
    }
  }
}


@media screen and (max-width:479px){
  .course-info{
    grid-template-columns: 100%;
  }
}


@media screen and (max-width:400px){
  .course{
    &__content{
      width: calc(100% + 20px);
      margin-left: -10px;
    }
  }

  .course-desc,
  .chapter,
  .resources,
  .chat{
    padding-left: 20px;
    padding-right: 20px;
  }
}
