.logo{
  display: block;
  flex-shrink: 0;
  width: 178px;
  img{
    width: 100%;
    display: block;
  }
}

.header{
  padding: 45px 0 15px;
  background-color: #fff;
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 30;
  transition: all .3s;
  border-bottom: 1px solid #E0E1E1;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__right{
    display: flex;
    align-items: center;
  }
  &__lang{
    font-weight: 600;
    font-size: 16px;
    line-height: 22px;
    margin-right: 13px;
  }
  &__btn{
    margin-left: 18px;
  }
  &__notification{
    &:hover{
      img{
        transform: translateY(-5px);
      }
    }
    img{
      transition: all .3s;
    }
  }
  &__lk{
    margin-left: 13px;
    position: relative;
    &-show{
      font-weight: 600;
      font-size: 10px;
      display: flex;
      align-items: center;
      cursor: pointer;
      &:hover{
        color: $secondary;
      }
    }
    &-icon{
      margin-right: 15px;
      flex-shrink: 0;
      width: 24px;
      height: 24px;
      position: relative;
      img{
        width: 100%;
        display: block;
      }
    }
    &-badge{
      position: absolute;
      background-color: $secondary;
      width: 8px;
      height: 8px;
      top: 2px;
      right: 0;
      border-radius: 50%;
    }
    &-hidden{
      position: absolute;
      left: -25px;
      top: 0;
      padding-top: 40px;
      @media screen and (min-width:993px){
        transition: all .3s;
        pointer-events: none;
        opacity: 0;
        transform: translateY(50%);
      }
    }
    &-inner{
      padding: 14px 0;
      gap: 12px;
      min-width: 165px;
    }
    &._active{
      .header__lk-hidden{
        opacity: 1;
        pointer-events: unset;
        transform: translateY(0);
      }
    }
  }
  &--index:not(.header--fix){
    background-color: transparent;
    border-color: transparent;
  }

}

.nav{
  display: flex;
  align-items: center;
  &__item{
    position: relative;
    font-size: 16px;
    line-height: 150%;
    font-weight: 600;
    &:not(:last-child){
      margin-right: 42px;
    }
    &._active{
      .nav__hidden{
        opacity: 1;
        pointer-events: unset;
        transform: translateY(0);
      }
    }
  }
  &__hidden{
    position: absolute;
    top: 0;
    left: -18px;
    min-width: 100%;
    padding-top: 30px;
    &-inner{
      padding: 11px 0;
      gap: 3px;
    }
    @media screen and (min-width:993px){
      transition: all .3s;
      opacity: 0;
      pointer-events: none;
      transform: translateY(50%);
    }
  }
  &__show{
    &:hover{
      cursor: pointer;
      color: $primary;
    }
  }
  &__sublink{
    position: relative;
    z-index: 3;
    padding: 0 16px;
    color: $gray;
    display: block;
    min-width: 165px;
    white-space: nowrap;
    line-height: 150%;
  }
}


@media screen and (max-width:992px){
  .nav{
    display: none;
  }

  .header{
    padding: 33px 0;
    &__lk{
      &-hidden{
        display: none;
        padding-top: 0;
        top: 40px;
      }
    }
  }
}


@media screen and (max-width:576px){
  .logo{
    width: 150px;
  }

  .header{
    &__lk{
      &-icon{
        margin-right: 5px;
      }
    }
  }
}
