.rent{
  padding-top: 75px;
  &__title{
    text-align: center;
  }
  &__subtitle{
    border-top: $border;
    padding-top: 38px;
    margin-top: 13px;
    font-weight: 600;
  }
  &__filter{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 18px 17px;
    margin-top: 16px;
    .form-group{
      input{
        height: 44px;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .rent{
    padding: 100px 0 50px;
    &__filter{
      grid-template-columns: 1fr 1fr;
    }
  }
}


@media screen and (max-width: 768px){
  .rent{
    &__subtitle{
      padding-top: 25px;
      text-align: center;
    }
    &__filter{
      grid-template-columns: 1fr;
    }
  }
}
