.about{
  padding: 125px 0 50px;
  overflow: hidden;
  &__title{
    text-align: center;
  }
  &__desc{
    margin: 31px auto 0;
    font-size: 16px;
    line-height: 156%;
    max-width: 772px;
    text-align: center;
  }
  &__img{
    display: block;
    margin: 21px auto 0;
    max-width: 867px;
    width: 100%;
    mix-blend-mode: multiply;
  }
  &__title2{
    margin-top: 85px;
    text-align: center;
  }
  &__desc2{
    margin-top: 31px;
  }
  &__quote{
    font-weight: 500;
    width: 1044px;
    max-width: 100%;
    margin: 83px auto 0;
    display: flex;
    &:before,
    &:after{
      flex-shrink: 0;
      font-size: 96px;
      color: #E1E7F0;
      transform: translateY(20px);
    }
    &:before{
      content: "";
      margin-right: 17px;
    }
    &:after{
      content: "";
      margin-left: 17px;
      align-self: flex-end;
    }
  }
}

.advantages--about{
  margin: 0;
}

.about-gallery{
  margin-top: 75px;
  &__img{
    width: 100%;
    height: 100%;
  }
  .swiper{
    &-slide{
      img{
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
    @media screen and (min-width: 992px){
      &-wrapper{
        transform: unset !important;
        display: grid !important;
        grid-template-columns: repeat(3,calc(100% / 3 - (20px / 3)));
        gap: 10px;
        border-radius: 15px;
        overflow: hidden;
      }
      &-slide{
        width: unset !important;
        margin: 0 !important;
        &:nth-child(2){
          grid-column: 2/4;
        }
        &:nth-child(3){
          grid-row: 2/3;
          grid-column: 1/3;
        }
      }
      &-pagination{
        display: none;
      }
    }
  }
}

.history{
  padding: 50px 0;
  overflow: hidden;
  &__slider{
    margin-top: 51px;
    padding-left: 50px;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      width: 100%;
      height: 1px;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
      background: #D9D9D9;
    }
    .swiper{
      &-container{
        width: 53px;
        height: 53px;
        position: relative;
        z-index: 2;
      }
      &-wrapper{
        align-items: stretch;
      }
      &-slide{
        display: flex;
        justify-content: center;
        align-items: center;
        height: auto;
        &-active{
          .history__toggle{
            width: 53px;
            height: 53px;
            background: #89C1F0;
          }
        }
      }
    }
  }
  &__content{
    margin-top: 44px;
    .swiper{
      &-container{
      }
    }
  }
  &__toggle{
    width: 21px;
    height: 21px;
    display: block;
    border-radius: 50%;
    background: #D9D9D9;
    transition: all .3s;
  }
  &__year{
    padding-left: 6px;
  }
  &__text{
    margin-top: 47px;
    max-width: 1182px;
  }
}

.workers{
  padding: 50px 0 10px;
  overflow: hidden;
  &__title{
    text-align: center;
  }
  &__tabs{
    margin-top: 28px;
  }
  &__group{
    &:not(:last-child){
      margin-bottom: 50px;
    }
  }
  &__slider{
    padding: 40px 0;
    .swiper{
      &-wrapper{
        align-items: stretch;
      }
      &-slide{
        height: auto;
        width: 282px;
      }
    }
  }
}

.become-worker{
  .feedback{
    @media screen and (min-width: 992px){
      &__inner{
        padding-right: 8.2%;
      }
      &__img{
        max-width: 420px;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .about{
    padding-top: 106px;
    &__desc{
      margin-top: 16px;
    }
    &__img{
      margin-top: 9px;
    }
    &__title2{
      margin-top: 35px;
    }
    &__desc2{
      margin-top: 22px;
    }
    &__quote{
      margin-top: 40px;
      font-size: 22px;
      line-height: 30px;
    }
  }

  .history{
    &__text{
      margin-top: 22px;
    }
  }

  .become-worker{
    .feedback{
      &__img{
        width: calc(100% - 100px);
        margin: -3% auto 0;
      }
    }
  }

  .about-gallery{
    position: relative;
    margin-top: 45px;
    .swiper{
      &-container{
      }
      &-pagination{
        position: static;
        background: rgba(255, 255, 255, 0.21);
        border-radius: 4px;
        width: unset;
        height: 31px;
        padding: 0 16px;
        display: inline-flex;
        align-items: center;
        color: #FFFFFF;
        font-size: 14px;
        span{
          margin: 0 3px;
        }
      }
    }
    &__img{
      border-radius: 27px;
      overflow: hidden;
      position: relative;
      padding-bottom: 87%;
      img{
        border-radius: inherit;
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        object-fit: cover;
      }
    }
    &__nav{
      position: absolute;
      right: 30px;
      bottom: 21px;
      display: flex;
      align-items: center;
    }
  }
}


@media screen and (max-width: 576px){
  .workers{
    .swiper{
      &-slide{
        &-active{
          &~.swiper-slide{
            filter: blur(2px);
          }
        }
      }
    }
  }
}


@media screen and (max-width: 400px){
  .about{
    &__img{
      margin: 9px 0 0 -10px;
      width: calc(100% + 20px);
    }
  }
}
