@import '../vars';
.modal {
    background: rgba(2, 25, 81, 0.33);
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100vh;
    overflow-y: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
    opacity: 0;
    pointer-events: none;
    &--active{
        opacity: 1;
        pointer-events: unset;
    }
    &__dialog{
        background-color: #fff;
        width: 700px;
        max-width: calc(100% - 20px);
        overflow-y: auto;
        max-height: 100vh;
        padding: 88px 75px;
        position: relative;
    }
    &__close{
        position: absolute;
        right: 24px;
        top: 24px;
    }
    &__desc{
        text-align: center;
        font-weight: 600;
        font-size: 24px;
        line-height: 125%;
        color: #000000;
    }
    &__title{
        color: #000000;
        text-align: center;
        margin-top: 30px;
    }
    &__form{
        margin-top: 55px;
    }
    .form-group{
        input{
            height: 56px;
        }
        label{
            font-weight: 600;
            font-size: 22px;
            line-height: 30px;
        }
        select{
            background-image: url(../img/select-arrow.svg);
            height: 61px;
            padding-top: 24px;
            &:focus,
            &.input-active {
                &+label {
                    top: 22px;
                }
            }
        }
        &:first-child{
            input,
            select{
               border-radius: 15px 15px 0 0;
            }
        }
        &:last-child{
            input,
            select{
                border-radius: 0 0 15px 15px;
            }
        }
        &:not(:last-child){
            margin-top: -1px;
        }
    }
    &__hint{
        font-size: 14px;
        line-height: 19px;
        margin-top: 20px;
        color: $gray3;
        a{
            font-weight: 700;
            text-decoration: underline;
        }
    }
    &__socials{
        margin-top: 56px;
        padding-top: 56px;
        border-top: 1px solid #E1E7F0;
        display: grid;
        grid-template-columns: 100%;
        gap: 15px;
    }
    &__social{
        height: 57px;
        border-radius: 15px;
        font-weight: 600;
        font-size: 14px;
        svg{
            margin-right: 20px;
        }
        span{
            min-width: 145px;
        }
    }
}




@media screen and (max-width: 768px){
    .modal{
        &__dialog{
            padding-left: 26px;
            padding-right: 26px;
        }
    }
}
