.profile-info{
  display: grid;
  grid-template-columns: repeat(4, calc(25% - (28px * 3 / 4)));
  gap: 28px;
  margin-top: 58px;
}


.profile-card{
  background: #FFFFFF;
  box-shadow: 0px 25px 33px rgba(225, 231, 240, 0.71);
  border-radius: 27px;
  padding: 23px 28px 26px;
  display: flex;
  flex-direction: column;
  align-items: center;
  &__title{
    padding-bottom: 19px;
    text-align: center;
    border-bottom: $border;
    width: 100%;
  }
  &__status{
    width: 100%;
    display: flex;
    align-items: center;
    margin-top: 40px;
    .profile-card__avatar{
      width: 40px;
      height: 40px;
      margin-right: 23px;
      flex-shrink: 0;
    }
    &-title{
      font-weight: 700;
    }
  }
  &__avatar{
    img{
      width: 100%;
      height: 100%;
      border-radius: 50%;
      object-fit: cover;
    }
  }
  &__btn{
    margin-top: auto;
    display: flex;
    align-items: center;
    padding-top: 15px;
    font-size: 13px;
    line-height: 20px;
    color: $primary;
    svg{
      margin-left: 5px;
    }
    &:hover{
      svg{
        transform: translateX(5px);
      }
    }
  }
  &__rating{
    position: relative;
    margin-top: 18px;
    &-num{
      position: absolute;
      top: 2px;
      right: -8px;
      width: 29px;
      height: 29px;
      border-radius: 50%;
      font-weight: 600;
      font-size: 16px;
      color: #fff;
      padding: 0;
    }
  }
  &__info{
    margin-top: 11px;
    width: 100%;
    &-title{
      font-size: 13px;
      line-height: 20px;
    }
    &-num{
      margin-top: 10px;
      font-weight: 600;
      font-size: 32px;
      line-height: 20px;
      color: $primary;
    }
  }
  &__props{
    width: 100%;
    padding-top: 20px;
    display: grid;
    grid-template-columns: 100%;
    gap: 5px;
    margin-top: auto;
  }
  &__prop{
    display: flex;
    align-items: center;
    font-size: 13px;
    line-height: 20px;
    color: $gray;
    &-num{
      flex-shrink: 0;
      width: 31px;
      height: 20px;
      margin-right: 10px;
      background: #DFE5ED;
      border-radius: 2px;
      display: flex;
      align-items: center;
      justify-content: center;
      color: $gray3;
    }
  }
}

.events{
  margin-top: 77px;
  &__grid{
    margin-top: 27px;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - (32px / 3)));
    gap: 16px;
  }
  &__wrap{
    display: grid;
    grid-template-columns: calc(100% - 310px) 280px;
    gap: 30px;
    margin-top: 45px;
  }
  &__items{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - (46px / 3)));
    gap: 23px;
  }
  &__more{
    margin: 88px auto 0;
    width: 175px;
    height: 46px;
    font-size: 16px;
  }
}

.event-card{
  padding: 31px 31px 11px;
  background: #FFFFFF;
  box-shadow: 0 25px 33px rgba(225,231,240,.71);
  border-radius: 20px;
  display: grid;
  grid-template-columns: 113px auto;
  gap: 11px;
  min-height: 120px;
  &__title{
    font-size: 16px;
    line-height: 20px;
    letter-spacing: -0.05em;
    color: $gray3;
  }
  &__items{
    margin-left: auto;
    width: 100%;
    max-width: 220px;
    display: flex;
    justify-content: space-between;
  }
  &__item{
    &--grow{
      flex-grow: 1;
    }
    &-title{
      display: flex;
      align-items: center;
      justify-content: flex-end;
      img{
        margin-right: 10px;
      }
    }
    &-num{
      font-weight: 700;
      font-size: 36px;
      letter-spacing: -0.05em;
    }
    &-desc{
      margin-top: 2px;
      font-size: 13px;
      line-height: 20px;
      color: $gray;
      text-align: right;
      display: flex;
      align-items: center;
      justify-content: flex-end;
    }
    .profile-card__prop-num{
      margin: 0 0 0 14px;
    }
  }
}

.events-filter{
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 51px;
  &__right{
    display: flex;
    align-items: center;
  }
  &__search{
    width: 192px;
    input{
      background-image: url(../img/search.svg);
      background-repeat: no-repeat;
      background-position: right 15px center;
      background-color: #fff;
      font-size: 16px;
      padding: 0 40px 0 14px;
    }
  }
  &__buttons{
    display: flex;
    align-items: center;
    margin-left: 26px;
  }
  &__btn{
    &:not(:last-child){
      margin-right: 18px;
    }
    &:hover{
      path{
        fill: $primary;
      }
    }
  }
}

.event-card2{
  padding: 20px 19px 28px;
  box-shadow: 0 25px 33px rgba(225,231,240,.71);
  background: #FFFFFF;
  border-radius: 27px;
  position: relative;
  &__img{
    position: relative;
    padding-bottom: 85%;
    border-radius: 18px;
    overflow: hidden;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      left: 0;
      top: 0;
      position: absolute;
    }
  }
  &__tags{
    margin-top: 16px;
    display: flex;
    align-items: center;
  }
  &__tag{
    height: 16px;
    display: flex;
    align-items: center;
    padding: 0 5px;
    border-radius: 3px;
    color: #fff;
    background-color: $primary;
    font-weight: 600;
    font-size: 10px;
  }
  &__title{
    margin-top: 7px;
    font-size: 16px;
    line-height: 150%;
    max-width: 95%;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp:2;
    -webkit-box-orient: vertical;
  }
  &__props{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 5px;
    margin-top: 15px;
    padding-top: 15px;
    border-top: $border;
  }
  &__prop{
    display: flex;
    align-items: center;
    font-size: 14px;
    line-height: 171%;
    img{
      margin-right: 8px;
      flex-shrink: 0;
    }
  }
  &__badge{
    position: absolute;
    left: 0;
    top: 55px;
    padding: 5px 14px;
    border-radius: 0px 8px 8px 0px;
    background-color: $secondary;
    color: #fff;
    z-index: 2;
    min-width: 132px;
    display: flex;
    align-items: center;
    img{
      margin-right: 8px;
      flex-shrink: 0;
    }
    &-title{
      font-size: 11px;
      line-height: 15px;
    }
    &-desc{
      font-size: 8px;
      line-height: 11px;
    }
  }
}

.achieve{
  background: #FFFFFF;
  box-shadow: 0px 25px 33px rgba(225, 231, 240, 0.71);
  border-radius: 27px;
  padding: 23px 28px;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: sticky;
  top: 110px;
  &__title{
    text-align: center;
    margin-bottom: 19px;
  }
  &__items{
    width: 100%;
  }
  &__item{
    padding: 18px 0;
    display: flex;
    align-items: center;
    border-top: 1px solid #F5F5F5;
    border-bottom: 1px solid #F5F5F5;
    &:not(:last-child){
      border-bottom: none;
    }
  }
  &__icon{
    width: 33px;
    height: 33px;
    background-color: $secondary;
    flex-shrink: 0;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 16px;
  }
  &__subtitle{
    font-size: 14px;
    line-height: 20px;
  }
  &__time{
    font-size: 16px;
    line-height: 20px;
    color: $gray;
  }
  &__link{
    margin-top: 23px;
    font-size: 16px;
    line-height: 19px;
    text-decoration-line: underline;
  }
}


@media screen and (max-width:1200px){
  .profile-info{
    grid-template-columns: repeat(3, calc(33.333% - (28px * 2 / 3)));
  }

  .events{
    &__grid{
      grid-template-columns: repeat(2, calc(50% - 8px));
    }
    &__items{
      grid-template-columns: repeat(2,calc(50% - 11.5px));
    }
  }
}


@media screen and (max-width:992px){
  .profile-info{
    grid-template-columns: repeat(2, calc(50% - 10px));
    gap: 28px 20px;
    margin-top: 41px;
  }

  .profile-card{
    padding: 23px 19px 27px;
    &__title{
      font-size: 20px;
    }
    &__status{
      margin-top: 27px;
    }
  }

  .events{
    &__grid{
      margin-top: 38px;
      grid-template-columns: 100%;
      gap: 22px;
    }
    &__wrap{
      padding-right: 88px;
      position: relative;
      grid-template-columns: 100%;
      margin-top: 35px;
    }
    &__right{
      position: absolute;
      right: 0;
      top: 0;
      height: 100%;
      z-index: 3;
    }
  }

  .events-filter{
    display: block;
    &__right{
      display: none;
    }
  }

  .achieve{
    &:not(._active){
      width: 68px;
      overflow: hidden;
      padding: 28px 18px 21px;
      .achieve{
        &__title{
          white-space: nowrap;
          opacity: 0;
          pointer-events: none;
          height: 0;
          overflow: hidden;
          margin: 0;
        }
        &__item{
          border: none;
          height: 33px;
          width: 33px;
          overflow: hidden;
          &:not(:last-child){
            margin-bottom: 43px;
          }
        }
        &__icon{
          margin-right: 0;
        }
        &__info{
          opacity: 0;
          pointer-events: none;
          white-space: nowrap;
        }
        &__link{
          opacity: 0;
          white-space: nowrap;
          height: 0;
          overflow: hidden;
          margin: 0;
        }
      }
    }
  }
}


@media screen and (max-width:700px){
  .events{
    &__items{
      grid-template-columns: 100%;
      gap: 30px;
    }
  }
}


@media screen and (max-width:480px){
  .profile-info{
    grid-template-columns: 100%;
  }
}


@media screen and (max-width:420px){
  .event-card{
    padding-left: 10px;
    padding-right: 10px;
  }
}
