.error{
  padding: 163px 0 163px;
  position: relative;
  &__img{
    width: 536px;
    max-width: 100%;
  }
  &__img2{
    position: absolute;
    right: 0;
    top: -103px;
    max-width: 75%;
  }
  &__content{
    position: relative;
    z-index: 2;
  }
  &__desc{
    margin-top: 100px;
    font-weight: 600;
    font-size: 22px;
    line-height: 136%;
    max-width: 676px;
    a{
      color: $primary;
    }
  }
  &__img3{
    display: none;
  }
}


@media screen and (max-width: 1600px){
  .error{
    &__img2{
      width: 1400px;
      max-width: 100%;
    }
  }
}


@media screen and (max-width: 992px){
  .error{
    padding: 0 0 78px;
    overflow: hidden;
    &__img2{
      display: none;
    }
    &__img3{
      pointer-events: none;
      margin: -40px -20px 0 0;
      width: calc(100% + 20px);
      display: block;
    }
    &__desc{
      text-align: center;
      margin-top: 50px;
    }
  }
}
