.edu{
  padding-top: 43px;
  &__inner{
    display: grid;
    grid-template-columns: 59.8% 40.2%;
  }
  &__content{
    padding-top: 7.4%;
    position: relative;
    z-index: 2;
  }
  &__img{
    img{
      width: 126%;
      margin-left: -26%;
      mix-blend-mode: multiply;
    }
  }
  &__title{
    line-height: 110%;
  }
  &__desc{
    font-weight: 400;
    margin-top: 46px;
    max-width: 488px;
    line-height: 156%;
    width: 80%;
    span{
      font-weight: 700;
    }
  }
  &__btn{
    width: 207px;
    height: 46px;
    margin-top: 44px;
  }
}

.for-who{
  padding: 3px 0 50px;
  overflow: hidden;
  &__title{
    text-align: center;
  }
  &__desc{
    margin: 12px auto 0;
    max-width: 772px;
    text-align: center;
  }
  &__slider{
    margin-top: 42px;
  }
  .swiper{
    &-container{
      position: relative;
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
      width: 378px;
    }
    &-button{
      &-disabled{
        opacity: 0;
      }
    }
  }
  .advantage-card{
    &__title{
      min-height: 90px;
    }
  }
}

.how{
  padding: 50px 0;
  overflow: hidden;
  &__title{
    text-align: center;
  }
  &__desc{
    margin: 22px auto 0;
    max-width: 772px;
    text-align: center;
  }
  &__slider{
    margin-top: 54px;
  }
  .swiper{
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
      width: 280px;
      max-width: calc(100% - 40px);
    }
  }
}

.program{
  overflow: hidden;
  &__title{
    text-align: center;
  }
  &__desc{
    margin: 22px auto 0;
    max-width: 772px;
    text-align: center;
  }
  &__grid{
    margin-top: 54px;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 16px));
    gap: 51px 32px;
  }
}


@media screen and (max-width: 992px){
  .for-who{
    &__title{
      font-size: 36px;
    }
    &__desc{
      margin-top: 29px;
    }
  }

  .program{
    &__grid{
      grid-template-columns: 100%;
    }
  }
}


@media screen and (max-width: 768px){
  .edu{
    padding: 100px 0 0;
    &__inner{
      grid-template-columns: 100%;
    }
    &__content{
      padding: 0;
    }
    &__desc{
      margin-top: 37px;
      width: 85%;
    }
    &__btn{
      margin-top: 32px;
    }
    &__img{
      margin-top: -63px;
      pointer-events: none;
      img{
        width: 100%;
        margin: 0;
      }
    }
  }

  .for-who{
    .swiper{
      &-slide{
        width: 359.48px;
        max-width: calc(100% - 20px);
      }
    }
  }
}
