.profile{
  padding: 77px 0 50px;
  &__title{
    text-align: center;
  }
  &__desc{
    margin: 15px auto 0;
    max-width: 572px;
    text-align: center;
    font-size: 12px;
    line-height: 15px;
  }
  &__upload{
    width: 174px;
    height: 174px;
    margin: 47px auto 0;
    position: relative;
    input{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 2;
      cursor: pointer;
      opacity: 0;
    }
    label{
      display: block;
      width: 100%;
      height: 100%;
      border-radius: 50%;
      border: 1px solid #E1E7F0;
      background: rgba(225, 231, 240, 0.71);
    }
    span{
      position: absolute;
      right: 9px;
      bottom: 6px;
      width: 44px;
      height: 44px;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      background-color: $primary;
    }
  }
  &__forms{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 57px 40px;
    margin-top: 79px;
  }
  &__form{
    display: grid;
    grid-template-columns: 100%;
    gap: 29px;
  }
  &__btn{
    width: 96px;
    height: 37px;
    margin-top: 20px;
    padding: 0;
  }
}


@media screen and (max-width: 768px){
  .profile{
    padding-top: 101px;
    &__upload{
      margin-top: 53px;
    }
    &__title{
      font-size: 36px;
    }
    &__desc{
      font-size: 16px;
      line-height: 25px;
      margin-top: 18px;
    }
    &__forms{
      grid-template-columns: 100%;
      margin-top: 58px;
    }
    &__form{
      &:not(:last-child){
        padding-bottom: 57px;
        border-bottom: $border;
      }
    }
  }
}
