.advantage-card{
  background: #FFFFFF;
  box-shadow: 0px 25px 33px rgba(225, 231, 240, 0.71);
  border-radius: 27px;
  padding: 23px 20px 56px 42px;
  min-height: 387px;
  &__icon{
    mix-blend-mode: multiply;
    height: 115px;
    display: flex;
    align-items: center;
    margin: 0 0 4px -13px;
    img{
      max-height: 100%;
    }
  }
  &__desc{
    margin-top: 10px;
  }
}

.apart-card,
.worker-card{
  background: #FFFFFF;
  border-radius: 27px;
  box-shadow: 0px 25px 33px rgba(225, 231, 240, 0.71);
  &__img{
    position: relative;
    border-radius: 18px;
    overflow: hidden;
    img{
      border-radius: 18px;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
      display: block;
      transition: all .3s;
    }
    &:hover{
      img{
        transform: scale(1.1);
      }
    }
  }
}

.apart-card{
  position: relative;
  &__top{
    position: relative;
    padding: 20px 20px 0;
  }
  &__img{
    padding-bottom: 84.1%;
  }
  &__badges{
    display: flex;
    flex-direction: column;
    align-items: center;
    top: 37px;
    left: 37px;
    position: absolute;
  }
  &__badge{
    min-width: 74px;
    margin: 0 auto 5px 0;
    height: 18px;
    display: flex;
    align-items: center;
    justify-content: center;
    white-space: nowrap;
    border-radius: 4px;
    font-size: 10px;
    color: #000000;
    background: #FFFFFF;
    padding: 0 7px;
    &.badge-purple{
      background: #BA63FF;
      color: #fff;
    }
  }
  &__favourite{
    position: absolute;
    top: 37px;
    right: 37px;
    width: 24px;
    height: 24px;
    display: block;
    background-image: url(../img/favourite.svg);
    background-size: 100% auto;
    background-repeat: no-repeat;
    background-position: center;
  }
  &__bottom{
    padding: 110px 23px 29px;
    margin-top: auto;
  }
  &__convinients{
    display: flex;
    align-items: center;
    img{
      &:not(:last-child){
        margin-right: 9px;
      }
    }
  }
  &__nav{
    margin-top: 21px;
    padding-top: 18px;
    border-top: 1px solid rgba(225, 231, 240, 0.71);
    display: flex;
    align-items: center;
  }
  &__toggle{
    margin-left: 9px;
    &:hover{
      path{
        stroke: $primary;
      }
    }
  }
  &__content{
    padding: 22px 23px 0;
    background-color: #fff;
    width: 100%;
    position: absolute;
    bottom: 133px;
    min-height: 88px;
    transition: all .3s;
  }
  &__title{
    font-size: 16px;
    line-height: 150%;
    max-width: calc(100% - 60px);
    display: block;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp:2;
    -webkit-box-orient: vertical;
  }
  &__city{
    margin-top: 5px;
  }
  &__rating{
    display: flex;
    align-items: center;
    margin-top: 12px;
    img{
      margin-right: 5px;
      flex-shrink: 0;
      width: 24px;
      display: block;
    }
  }
  &__hidden{
    display: none;
  }
  &__props{
    margin-top: 25px;
    display: grid;
    grid-template-columns: 100%;
    gap: 5px;
  }
  &__prop{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &._open{
    .apart-card__toggle{
      svg{
        transform: rotate(-180deg);
      }
    }
    .apart-card__content{
      padding-top: 30px;
    }
  }
}

.worker-card{
  padding: 19px 19px 34px;
  &__img{
    padding-bottom: 106.6%;
  }
  &__name{
    margin-top: 20px;
    span{
      display: block;
      font-weight: 700;
    }
  }
  &__prof{
    margin-top: 2px;
  }
}

.vacancy-card{
  padding: 13px 47px 56px;
  background: #FFFFFF;
  border-radius: 27px;
  box-shadow: 0px 25px 33px rgba(225, 231, 240, 0.71);
  &__icon{
    height: 187px;
    display: flex;
    align-items: center;
    margin-left: -9px;
    img{
      max-height: 100%;
      max-width: 100%;
    }
  }
  &__salary{
    margin-top: 8px;
    font-weight: 600;
    font-size: 18px;
    line-height: 167%;
  }
  &__conditions{
    margin: 18px 0 30px;
  }
  &__condition{
    &:not(:last-child){
      margin-bottom: 14px;
    }
    &-desc{
      margin-top: 4px;
    }
  }
}

.cooperate-card{
  padding: 70px 40px 46px;
  border-radius: 27px;
  text-align: center;
  position: relative;
  display: flex;
  flex-direction: column;
  &__desc{
    margin-top: 13px;
  }
  &__img{
    margin: 4px 0 2px;
    height: 175px;
    img{
      margin: 0 auto;
      display: block;
      height: 100%;
      width: auto;
    }
  }
  &__btn{
    width: 205px;
    height: 43px;
    margin: auto auto 0;
  }
}

.cooperate-card2{
  padding-bottom: 0;
  .cooperate-card__img{
    height: 215px;
    margin-bottom: 0;
    display: flex;
    align-items: flex-end;
    justify-content: center;
    img{
      max-width: 100%;
      transform: translateX(-5%);
    }
  }
  .cooperate-card__btn{
    position: absolute;
    left: 50%;
    bottom: 48px;
    transform: translateX(-50%);
  }
}

.review-card{
  &__top{
    display: flex;
    align-items: center;
  }
  &__img{
    width: 40px;
    height: 40px;
    flex-shrink: 0;
    margin-right: 23px;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 50%;
    }
  }
  &__name,
  &__date{
    font-size: 16px;
    line-height: 20px;
    letter-spacing: 0.02em;
  }
  &__name{
    font-weight: 700;
  }
  &__desc{
    max-width: 461px;
    margin-top: 17px;
    overflow: hidden;
    display: -webkit-box;
    //-webkit-line-clamp:3;
    -webkit-box-orient: vertical;
  }
  &__all{
    display: none;
    align-items: center;
    margin-top: 10px;
    font-weight: 700;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: 0.02em;
    svg{
      margin-left: 11px;
    }
  }
  &._cut{
    .review-card__all{
      display: flex;
    }
  }
  &._show{
    .review-card__desc{
      -webkit-line-clamp: unset !important;
    }
    .review-card__all{
      span{
        display: none;
      }
      &:before{
        content: '';
      }
    }
  }
}

.who-card{
  padding: 84px 38px 16px;
  border-radius: 27px;
  text-align: center;
  &__desc{
    margin-top: 23px;
  }
  &__img{
    max-height: 175px;
    display: flex;
    justify-content: center;
    margin-top: 21px;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
}

.how-card{
  background: #FFFFFF;
  border-radius: 27px;
  padding: 67px 40px 35px;
  position: relative;
  transition: all .3s;
  &:hover{
    box-shadow: 0px 25px 33px rgba(225, 231, 240, 0.71);
  }
  &__num{
    position: absolute;
    left: 50%;
    top: 0;
    transform: translate(-50%,-50%);
    width: 56px;
    height: 56px;
    border-radius: 18px;
    background-color: $gray3;
    font-weight: 400;
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__desc{
    margin-top: 7px;
  }
}

.program-card{
  padding: 93px 44px 81px;
  border-radius: 27px;
  position: relative;
  overflow: hidden;
  &__img{
    position: absolute;
    top: 78px;
    right: 0;
    width: 280px;
    max-width: 100%;
  }
  &__subtitle{
    font-weight: 700;
    font-size: 16px;
    margin-top: 18px;
  }
  &__title,
  &__subtitle,
  &__list,
  &__btn{
    position: relative;
    z-index: 2;
  }
  &__list{
    margin-top: 13px;
    max-width: 430px;
    li{
      &:nth-child(1),
      &:nth-child(2){
        max-width: 293px;
      }
    }
  }
  &__btn{
    width: 178px;
    height: 44px;
    margin-top: 30px;
    font-size: 16px;
  }
  &__num{
    position: absolute;
    right: 0;
    bottom: 0;
    pointer-events: none;
  }
}

.effective-card{
  box-shadow: 0px 25px 33px rgba(225, 231, 240, 0.71);
  background: #FFFFFF;
  border-radius: 27px;
  padding: 82px 40px 65px;
  display: grid;
  grid-template-columns: auto 44%;
  gap: 34px;
  &__img{
    img{
      width: 100%;
    }
  }
  &__desc{
    margin-top: 20px;
  }
}

.offer-card{
  position: relative;
  overflow: hidden;
  background: #FFFFFF;
  box-shadow: 0px 25px 33px rgba(225, 231, 240, 0.71);
  border-radius: 27px;
  &__wrap{
    padding-left: 40px;
    padding-right: 40px;
    position: relative;
    z-index: 2;
  }
  &__header{
    padding-top: 36px;
    padding-bottom: 27px;
    border-bottom: 1px solid #E1E7F0;
  }
  &__desc{
    margin-top: 3px;
  }
  &__percent{
    padding-top: 21px;
    padding-bottom: 14px;
    border-bottom: 1px solid #E1E7F0;
    &-num{
      font-weight: 900;
      font-size: 48px;
      line-height: 30px;
      color: #86D1FB;
      margin-bottom: 3px;
      span{
        font-weight: 800;
      }
    }
  }
  &__bottom{
    padding-top: 37px;
    padding-bottom: 65px;
  }
  &__list{
    li{
      &:before{
        background-image: url(../img/list-icon2.svg);
        width: 20px;
        height: 20px;
      }
    }
  }
  &__btn{
    margin-top: 45px;
    width: 138px;
    height: 37px;
    padding: 0;
  }
  &__vip{
    position: absolute;
    top: 166px;
    right: -37px;
  }
  &--type2{
    border: 2px solid $secondary;
    .offer-card__percent-num{
      color: $secondary;
    }
  }
  &--type3{
    background: #FFD585;
    .offer-card{
      &__header{
        border-color: #FFED8F;
      }
      &__percent{
        border-color: #FFED8F;
        &-num{
          color: #FFFFFF;
        }
      }
      &__list{
        li{
          &:before{
            background-image: url(../img/list-icon.svg);
          }
        }
      }
    }
  }
}


@media screen and (max-width: 1200px){
  .effective-card{
    grid-template-columns: 100%;
    position: relative;
    padding: 42px 34px 55px;
    &__title{
      display: flex;
      align-items: center;
      min-height: 105px;
      position: relative;
      z-index: 2;
      padding-right: 110px;
    }
    &__desc{
      margin-top: 8px;
    }
    &__img{
      height: 105px;
      position: absolute;
      top: 42px;
      right: 40px;
      img{
        height: 100%;
        width: auto;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .review-card{
    padding: 28px 30px 23px;
    border: 1px solid $gray3;
    border-radius: 27px;
    transition: all .3s;
  }
}


@media screen and (max-width: 576px){
  .program-card{
    padding: 46px 43px 105px;
    &__img{
      position: static;
      margin-bottom: 8px;
      display: block;
      width: unset;
    }
    &:nth-child(2){
      .program-card__img{
        margin-left: auto;
        margin-right: -43px;
      }
    }
    &__list,
    &__btn{
      margin-top: 45px;
    }
  }

  .effective-card{
    &__title{
      font-size: 22px;
    }
  }
}


@media screen and (max-width: 400px){
  .advantage-card{
    padding-left: 20px;
    padding-right: 10px;
  }

  .vacancy-card{
    padding-left: 25px;
    padding-right: 25px;
  }

  .how-card{
    padding-left: 20px;
    padding-right: 20px;
  }

  .program-card{
    padding-left: 20px;
    padding-right: 20px;
    padding-bottom: 80px;
    &:nth-child(2){
      .program-card__img{
        margin-right: -20px;
      }
    }
  }

  .effective-card{
    padding: 20px 20px 30px;
    &__img{
      top: 20px;
      right: 20px;
      height: 95px;
    }
    &__title{
      padding-right: 100px;
      min-height: 95px;
    }
  }

  .offer-card{
    &__wrap{
      padding-left: 20px;
      padding-right: 20px;
    }
  }
}
